(function() {
'use strict';

angular
    .module('front')
    .controller('LoginController', LoginController);

/** @ngInject */
function LoginController($route, $filter, $location, authService) {
    var vm = this;

    vm.init = function() {
    	if(authService.isLogged()) {
    		$location.path($filter('url', 'main'));
    	}

        var queryMessage = $location.search().message;
        if(queryMessage) {
            switch(queryMessage) {
                case 'access_denied':
                    vm.errorMessage = 'Acesso negado.';
                    break;

                case 'token_expired':
                    vm.errorMessage = 'Acesso expirado. Refaa seu login.';
                    break;
            }
        }
    };

    vm.submit = function() {
    	authService.login(vm.form.username, vm.form.password)
    	.then(function(data) {
    		$route.reload();
    	})
    	.catch(function(err) {
    		console.log(err);
    	});
    };

    vm.init();
}
})();

(function() {
'use strict';

angular
    .module('front')
    .service('authInterceptor', authInterceptor);

/** @ngInject */
function authInterceptor($q, $injector) {
    var interceptor = this;

    interceptor.responseError = function(response) {
        if(response.status == 500 && response.data.error == "TokenExpiredError: jwt expired") {
            $injector.get('authService').logout();
            return $q.reject(response);
        } else {
            return $q.reject(response);
        }
    };

    return interceptor;
}

})();

